.mbti-layout {
  width: 100vw;
  font-size: 14px;
  display: flex;
  justify-content: center;
  background-color: #eee;
  height: calc(var(--ch,1vh)*100);
}

.startPageLayout{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  background-color: #FFF7E3;
  width: 100%;
  max-width: 500px;
  height: 100%;
  color: #413730;
}
.startLogo{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* font-size: 35px; */
  font-weight: 700;
}

.startLogo div:nth-child(1){
  /* background-color: #413730; */
  color: #FF7A01;
  width: 300px;
  height: 100px;
  display: flex;
  margin-top: 30px;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  font-size: 55px;
  font-family: 'ONE-Mobile-POP';
}

.startLogo div:nth-child(2){
  margin-top: -15px;
  color: #333333;
  font-size: 45px;
  font-family: 'ONE-Mobile-POP';
}

.startButton{
  font-size: 25px;
  font-weight: 700;
  margin-top: 20px;
  align-items: center;
  color: white;
  cursor: pointer;
}

.startBottomPageLayout{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}


.questionLayout{
  display: flex;
  flex-direction: column;
  background-color: #8076FD;
  width: 100%;
  max-width: 900px;
  height: 100%;
  overflow: hidden;
}
.mbtiTitle{
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  padding: 15px;
  box-sizing: border-box;
  font-weight: 700;
}

.mbtiTitle div:nth-child(1){
  display: flex;
  flex:1;
}

.questionList{
  flex-direction: column;
  height: 100%;
  overflow: scroll;
  scrollbar-width: none;
}

.questionList::-webkit-scrollbar{
  display: none;
}

.questionItemLayout{
  display: flex;
  flex:1;
  margin: 10px 15px 5px 15px;
}
.answerItemLayout{
  width: 100%;
  background-color: #fff;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
}

.profileImg{
  display: flex;
  flex-direction: column;
  width: 40px;
  height: 40px;
  background-color: #a1b6e9;
  border-radius: 15px;
  margin-right: 5px;
  align-items: center;
  justify-content: center;
}

.profileImg div:nth-child(1){
  background-color: #cbd6f2;
  width: 8px;
  height: 8px;
  border-radius: 4px;
}

.profileImg div:nth-child(2){
  background-color: #cbd6f2;
  width: 16px;
  height: 8px;
  border-radius: 8px 8px 0px 0px;
  margin-top:2px;
}

.chatListLayout{
  display: flex;
  flex-direction: column;
}

.chatBox{
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 60vw;
  margin: 3px 0px 12px 0px;
}

.chatBox div:nth-child(1){
  color: #E9ABFE;
  font-size: 12px;
  margin-top: 8px;
}

.chatBox div:nth-child(2){
  padding: 7px 10px 7px 10px;
  background-color: #E9ABFE;
  margin-left: -4px;
  border-radius: 10px;
  text-align: left;
  
}

.aChatBox{
  display: flex;
  width: 100%;
  color: #969696;
  font-size: 22px;
  border-bottom: 1px solid #eee;
  margin-bottom: 25px;
}

.aChatBox div:nth-child(1){
  display: flex;
  flex:1;
  padding: 10px;
  box-sizing: border-box;
}

.aChatBox div:nth-child(2){
  padding: 10px;
}

.answerBox{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  text-align: center;
  box-shadow: 2px 2px 0px #ededed;
  border: 1px solid #ededed;
  margin: 0px 20px 15px 20px;
  border-radius: 10px;
  cursor: pointer;
}

.resultBox{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  text-align: center;
  box-shadow: 2px 2px 0px #ededed;
  border: 1px solid #ededed;
  background-color: #413730;
  color: white;
  margin: 0px 20px 15px 20px;
  border-radius: 10px;
  font-size: 30px;
  font-weight: 700;
  cursor: pointer;
}

@font-face {
  font-family: 'ONE-Mobile-POP';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-POP.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}